	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Незаменимое и простое упражнение –не понадобиться ничего, кроме земли(пола) и Вашего тела. Отжимания – базовое (или многосуставное) упражнение. По своей сути, отжимания - это тот же классический жим лежа наоборот. Отличное упражнение для начинающих.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n    <li>Лягте на пол. Примите упор лежа, удерживая тело на выпрямленных руках.</li>\n    <li>Исходное положение – руки немного шире плеч, ладони на одном уровне с плечами.</li>\n    <li>На вдохе - сохраняя тело прямым, сгибайте руки, пока грудью не коснетесь пола.</li>\n    <li>На выдохе – вернитесь в исходное положение.</li>\n    <li>Повторите нужное число раз.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>От положения рук сильно зависит то, какие мышцы задействованы: руки поставлены высоко или узко – больше работает трицепс, чем шире руки – тем сильнее задействована грудь.</li>\n  <li>Хотите чтобы руки были задействованы минимально – делайте отжимания, используя какую-нибудь подставку для ног – если ноги будут выше плечей, то нагрузка ложится в основном на грудь и плечи.</li>\n  <li>Попробуйте «поиграть» с амплитудой упражнения – если будете делать отжимания в верхней части амплитуды, то работают трицепсы, если в нижней – грудь.</li>\n  <li>Если вы делаете уже много повторений, то используйте отягощения. Подбирайте его таким, чтобы выполнить 8-10 раз.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/XIHO5t_VBPQ\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/otjimania-ot-pola","efficiency":"5","title":"Отжимания от пола","image":"/img/0-04-0.png","image_2":"/0-04-1.jpg","image_3":"/0-04-2.jpg","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"2","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"ownweight","inventory":"Вес","filterplace":""}}},"pathContext":{}}

